.summary {
  text-align: center;
  max-width: 45rem;
  width: 90%;
  margin: auto;
  margin-top: -8rem;
  position: relative;
  background-color: #383838;
  color: white;
  border-radius: 14px;
  padding: 1rem;
  box-shadow: 0 1px 18px 10px rgba(0, 0, 0, 0.25);
}

.summary h2 {
  font-size: 2rem;
  margin-top: 0;
}

@media only screen and (max-width: 480px) {
  .summary {
    max-width: 45rem;
    width: 85%;
    padding: 1rem;
  }

  .summary h2 {
    font-size: 1.4rem;
    margin-top: 0;
  }

  .summary p {
    font-size: 0.75rem;
  }
}
